@import url('https://fonts.googleapis.com/css2?family=Inter:wght@200;500;700&display=swap');
@import url('./transitions.css');

.home-background {
    background-color: #edf2f4;
    border-bottom: 1px solid #1b2863;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    top: 55px;
    height: calc(100vh - 55px);
    width: 100vw;
}

.circle {
    height: 120%;
    width: 55vw;
    background-image: linear-gradient(to right, #9FB4BC, #2B2D42);
    border-top-left-radius: 50%;
    border-bottom-left-radius: 50%;
    display: inline-block;
    animation: 0.75s ease 0s 1 fadeIn;
}

#intro,
#major {
    font-size: 1.75rem;
    font-weight: 500;
}

#name {
    font-size: 3.5rem;
}

#class {
    font-weight: 500;
    font-size: 2rem;
}

.flex {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    height: 95vh;
    width: 100%;
}

.options {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 25%;
}

.option {
    width: 100%;
    height: 30%;
    border-radius: 5px;
    margin-top: 25px;
    text-align: center;
}

.home-title-container {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    animation: 1s ease 0s 1 slideAndFadeInFromLeft;
}

.home-title {
    margin: 0;
}

.subtitle {
    margin: 0;
    font-weight: 600;
}

.vertical-bar-1 {
    width: 61px;
    background-color: #8D99AE;
    height: 100%;
}

@media only screen and (max-width: 1100px) {
    .circle {
        width: 0;
    }
    #name {
        font-size: 50px;
    }
    #class {
        font-size: 30px;
    }
    #intro,
    #major {
        font-size: 25px;
    }
}