.projects-background {
    background-color: #edf2f4;
    animation: 1s ease 0s 1 fadeIn;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    top: 55px;
}

.projects-title {
    margin-bottom: 15px;
    font-size: 4.5rem;
    text-align: center;
    animation: 1s ease 0s 1 slideAndFadeInFromLeft;
}

.header {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 30vh;
    width: 100%;
    background-color: #333533;
    color: white;
}

.filters {
    display: flex;
    justify-content: space-evenly;
    animation: 1s ease 0s 1 slideAndFadeInFromLeft;
}

.filter {
    margin: 0px 0px;
    padding-left: 10px;
    padding-right: 10px;
    padding-bottom: 50px;
}

@media only screen and (max-width: 1200px) {
    .projects-title {
        font-size: 3rem;
        margin: 0.25rem;
    }
    .filters {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 0;
    }
    .filter {
        display: none;
    }
}