.contact-background {
    background-color: #edf2f4;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    top: 55px;
    height: calc(100vh - 55px);
    width: 100vw;
}

.contact-container {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 5rem;
}

.left {
    display: flex;
    flex-direction: column;
    text-align: left;
    align-items: flex-start;
    width: 30%;
    animation: 1s ease 0s 1 slideAndFadeInFromLeft;
}

.right {
    width: 35%;
    animation: 1s ease 0s 1 slideAndFadeInFromLeft;
}

.title {
    font-size: 3.5rem;
}

.text {
    margin: 5px 0px;
}

@media only screen and (max-width: 1050px) {
    .contact-container {
        flex-direction: column;
        width: 70%;
        gap: 1rem;
    }
    .left, .right {
        width: 100%;
    }
    .left {
        align-self: flex-start;
    }
    .title {
        font-size: 2.5rem;
    }
}

@media screen and (orientation: landscape) and (max-width: 1050px) {
    .contact-background {
        position: relative;
        height: 130vh;
    }
}

@media screen and (orientation: landscape) and (max-width: 670px) {
    .contact-background {
        position: relative;
        height: 140vh;
    }
}