.row {
    padding: 0.5rem;
    width: 100%;
    height: fit-content;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 1rem;
    animation: 1s ease 0s 1 slideAndFadeInFromLeft;
}

.cell {
    display: flex;
    flex-direction: column;
    border-radius: 5px;
    border: 1px solid #dad7cd;
    justify-content: space-evenly;
    height: 25vh;
    width: 30%;
    background-color: #edf2f4;
    padding: 1rem;
}

.github {
    color: black;
}

.cell-text {
    text-align: left;
    position: relative;
    left: 25px;
    top: 25px;
    width: 80%;
    margin: 1px;
}

.image {
    width: 15%;
    object-fit: contain;
}

.images {
    margin-top: 40px;
    margin-bottom: 20px;
    display: flex;
    justify-content: space-evenly;
}

.skills {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
    height: 40%;
}

@media only screen and (max-width: 1200px) {
    .row {
        flex-direction: column;
        height: fit-content;
    }
    .cell {
        width: 90%;
        height: 25%;
    }
    .image {
        width: 10%;
    }
}