.form {
    border-radius: 5px;
    width: 100%;
}

.horizontal-bar {
    width: 100%;
    margin-bottom: 15px;
    background-color: #2B2D42;
    height: 2px;
}

#title {
    padding-bottom: 10px;
    font-size: 40px;
}

input[type=submit] {
    width: 40%;
    height: 40px;
    background-color: #2d2d42;
    color: white;
    padding: 7px 10px;
    margin: 9px 0;
    border: 1px solid #c2c0be;
    border-radius: 4px;
    cursor: pointer;
}

input[type=text] {
    width: 100%;
    padding: 10px 10px;
    margin: 9px 0;
    display: inline-block;
    border: 1px solid #c2c0be;
    border-radius: 4px;
    box-sizing: border-box;
    background-color: #cacdd2;
}

textarea {
    width: 100%;
    resize: none;
    padding: 10px 10px;
    margin: 9px 0;
    display: inline-block;
    border: 1px solid #c2c0be;
    border-radius: 4px;
    box-sizing: border-box;
    background-color: #cacdd2;
}

input[type=email] {
    width: 100%;
    padding: 10px 10px;
    margin: 9px 0;
    display: inline-block;
    border: 1px solid #c2c0be;
    border-radius: 4px;
    box-sizing: border-box;
    background-color: #cacdd2;
}

.contact {
    display: flex;
    flex-direction: column;
}