.about-background {
    background-color: #edf2f4;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    position: relative;
    top: 55px;
    height: calc(100vh - 55px);
    width: 100vw;
}

#about-title {
    font-size: 3.5rem;
    width: 100%;
    margin: 0;
}

#headshot {
    width: 25vw;
    height: 60vh;
    object-fit: cover;
    object-position: 50% 25%;
    filter: saturate(1.2);
    animation: 1s ease 0s 1 slideAndFadeInFromLeft;
}

#about-me {
    animation: 1s ease 0s 1 slideAndFadeInFromLeft;
}

.about-box {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width: 90%;
    gap: 1rem;
}

.about-skills {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width: 90%;
    gap: 1rem;
}

.about-skills-image {
    width: 75px;
    height: auto;
}

.v-box {
    width: 40%;
    display: flex;
    justify-content: space-evenly;
    flex-direction: column;
}

.about-text {
    width: 100%;
    margin-top: 15px;
    margin-bottom: 15px;
}

#download-resume {
    width: 30%;
    text-decoration: none;
    color: white;
    font-size: 0.8rem;
    border: 1px solid #c2c0be;
    border-radius: 4px;
    padding: 10px 0px;
    background-color: #2d2d42;
    text-align: center;
    margin-top: 15px;
}

@media screen and (max-width: 1500px) {
    #about-title {
        font-size: 2.5rem;
    }
}

@media screen and (max-width: 1150px) {
    .about-box {
        flex-direction: column;
    }
    #headshot {
        width: 70vw;
        height: 30vh;
    }
    .v-box {
        width: 70vw;
    }
    .about-skills-image {
        width: 50px;
    }
    .about-text {
        font-size: .75rem;
    }
    #download-resume {
        width: 100%;
    }
}

@media screen and (max-width: 850px) {
    #download-resume {
        font-size: 0.6rem;
    }
    #about-title {
        font-size: 2rem;
    }
}

@media screen and (orientation: landscape) and (max-width: 1150px) {
    .about-background {
        position: relative;
        height: 125vh;
    }
}